import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IonicPage, NavController, NavParams,AlertController,MenuController } from 'ionic-angular';

import { ApiProvider } from '../../providers/api/api';
import { AuthProvider } from '../../providers/auth/auth';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the DetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detail',
  templateUrl: 'detail.html',
})
export class DetailPage {
  id:any;
  user_id:any;
  prolikeList:any;
  url:any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public api:ApiProvider,
    private AuthService: AuthProvider,
    public alertCtrl: AlertController,
    private service: ServiceProvider
    ) {
  
    this.id = this.navParams.get('id');
   // alert(this.id);
    this.user_id = AuthService.getuserid();
    this.alsolikeList();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailPage');
  }


  alsolikeList() {
    this.api.post('alsolikelist',{product_id:this.id,user_id:this.user_id}).subscribe((response : any)  => {
      console.log(response);
      if(response.Ack === 1){
        this.prolikeList = response.like_details;
        this.url = "http://111.93.169.90/";
        console.log(this.prolikeList);
      }
    }, err => {
      this.service.popup('Alert', 'Already Registered');
    });
    
  }

}



